<template>
  <form
    @submit.prevent="handleSubmit"
    class="dataForm-container"
    ref="dataForm"
  >
    <div class="nickName">
      <input
        type="text"
        class="name"
        placeholder="用户昵称"
        maxlength="10"
        v-model="formData.nickName"
      />
      <span class="count-name">{{ this.formData.nickName.length }}/10</span>
      <div class="err" v-show="nickNameErr">请填写昵称</div>
    </div>
    <div class="message">
      <textarea
        cols="30"
        rows="10"
        class="message-in"
        placeholder="请输入内容"
        maxlength="300"
        v-model="formData.content"
      ></textarea>
      <span class="count-message">{{ this.formData.content.length }}/300</span>
      <div class="err" v-show="contentErr">请填写内容</div>
    </div>
    <button class="submit" :disabled="isSubmit">
      {{ isSubmit ? "提交中..." : "提交" }}
    </button>
  </form>
</template>

<script>
export default {
  data() {
    return {
      formData: {
        nickName: "",
        content: "",
      },
      nickNameErr: false,
      contentErr: false,
      isSubmit: false,
    };
  },
  methods: {
    async handleSubmit() {
      this.nickNameErr = this.formData.nickName ? false : true;
      this.contentErr = this.formData.content ? false : true;
      if (this.nickNameErr || this.contentErr) {
        return;
      }
      this.isSubmit = true;
      this.$emit("submit", this.formData, (msg) => {
        this.$showMessage({
          content: msg,
          type: "error",
          container: this.$refs.dataForm,
          duration: 1000,
          callBack: () => {
            this.isSubmit = false;
            this.formData.nickName = "";
            this.formData.content = "";
          },
        });
      });
    },
  },
};
</script>

<style scoped lang="less">
.dataForm-container {
  width: 100%;
  .nickName {
    width: 300px;
    margin: 10px 0px;
    position: relative;
    .name {
      width: 100%;
      border: 1px dashed #ccc;
      box-sizing: border-box;
      outline: none;
      padding: 8px 10px;
      border-radius: 5px;
      height: 40px;
      letter-spacing: 0.5px;
      &:focus {
        border: 1px dashed lightblue;
      }
    }
    .count-name {
      position: absolute;
      right: 4px;
      top: 50%;
      transform: translateY(-50%);
      font-size: 12px;
      color: #ccc;
    }
  }
  .message {
    width: 100%;
    height: 120px;
    margin: 35px 0px;
    position: relative;
    .message-in {
      width: 100%;
      height: 100%;
      box-sizing: border-box;
      padding: 8px 10px;
      border: 1px dashed #ccc;
      resize: none; /* 阻止用户改变文本框大小 */
      border-radius: 5px;
      outline: none;
      &:focus {
        border: 1px dashed lightblue;
      }
    }
    .count-message {
      position: absolute;
      right: 5px;
      bottom: 2px;
      font-size: 12px;
      color: #ccc;
    }
  }
  .submit {
    width: 100px;
    height: 34px;
    background: rgb(124, 195, 219);
    border-radius: 5px;
    border: none;
    color: #fff;
    letter-spacing: 1px;
    cursor: pointer;
    &:disabled {
      opacity: 0.5;
      cursor: not-allowed;
    }
  }
  .err {
    position: absolute;
    color: #f40;
    bottom: -18px;
    left: 2px;
    font-size: 12px;
  }
}
</style>