//公共远程或许数据的代码
//具体组件需要提供一个远程获取方法，fetchData
export default function(defaultDataValue = null){
    return  {
        data(){
            return{
                isLoading:true,
                data:defaultDataValue
            }
        },
        async created(){
            this.data = await this.fetchData();
            this.isLoading = false;
        }
    }
}