<template>
  <i class="iconfont icon-container" :class="fontClass"></i>
</template>

<script>
const classMap = {
  home: "icon-shouye",
  wx: "icon-weixin",
  qq: "icon-rfqquantity",
  email: "icon-24gl-mailboxEmpty",
  phone: "icon-wifidianhuaqia",
  me: "icon-guanyuwomen",
  article: "icon-bianjiwenzhang_huaban",
  messageBoard: "icon-liuyanban-05",
  project: "icon-xiangmushishi",
  arrowDown: "icon-xiangxia",
  arrowUp: "icon-xiangshang",
  error: "icon-xiangmushishi",
};
export default {
  props: {
    type: {
      type: String,
      required: true,
    },
  },
  computed: {
    //图标类样式
    fontClass() {
      return classMap[this.type];
    },
  },
};
</script>

<style scoped>
@import url(//at.alicdn.com/t/c/font_4000440_mqss99gpd6q.css);
.icon-container {
  color: inherit;
  font-size: inherit;
}
</style>
