<template>
  <ul class="classList-container">
    <li v-for="(item, i) in list" :key="i">
      <span @click="handleClick(item)" :class="{ active: item.isSelect }">{{
        item.name
      }}</span>
      <span
        v-if="item.aside"
        class="aside"
        :class="{ active: item.isSelect }"
        @click="handleClick(item)"
        >{{ item.aside }}
      </span>
      <ClassList :list="item.children" @select="handleClick" />
    </li>
  </ul>
</template>

<script>
export default {
  name: "ClassList",
  //[{name:xxx, isSelect:true, children:[{name:xxx, isSelect:true, children:[]}]}]
  props: {
    list: { type: Array, default: () => [] },
  },
  methods: {
    handleClick(item) {
      this.$emit("select", item);
    },
  },
};
</script>

<style scoped lang="less">
@import "~@/style/common.less";
.classList-container {
  list-style: none;
  padding: 0;
  cursor: pointer;
  .classList-container {
    margin-left: 1em;
  }
  li {
    min-height: 40px;
    line-height: 40px;
    font-size: 14px;
    .active {
      font-weight: bold;
      color: @warn;
      cursor: text;
    }
  }
  .aside {
    font-size: 10px;
    color: @gray;
    margin-left: 1em;
  }
}
</style>