import ins from './request'

//获取分页博客
export async function getBlog(page=10,limit=10,categoryid=-1){
    return await ins.get('/api/blog',{
        params:{
            page,
            limit,
            categoryid,
        }
    });
}


//获取所有博客分类
export async function getBlogType(){
    return await ins.get('/api/blogtype');
}


//获取单个博客
export async function getSoloBlog(id=1){
    return await ins.get(`/api/blog/:${id}`)
}

//提交评论
export async function postComment(commentInfo){
    return await ins.post('/api/comment',commentInfo)
}

//获取分页评论
export async function getComment(blogId,page=1,limit=10){
    return await ins.get('/api/comment',{
        params:{
            blogId,
            page,
            limit,
        }
    })
}