<template>
  <div class="toTop-container" v-show="show" @click="handlerClick">
    <span>TOP</span>
  </div>
</template>

<script>
export default {
  data() {
    return {
      show: false,
    };
  },
  created() {
    this.$bus.$on("mainScroll", this.handlerScroll);
  },
  destroyed() {
    this.$bus.$off("mainScroll", this.handlerScroll);
  },
  methods: {
    handlerScroll(dom) {
      if (!dom) {
        this.show = false;
        return;
      }
      this.show = dom.scrollTop > 500;
    },
    handlerClick() {
      this.$bus.$emit("setMainScroll", 0);
    },
  },
};
</script>

<style scoped lang="less">
.toTop-container {
  width: 50px;
  height: 50px;
  background: lightblue;
  border-radius: 50%;
  text-align: center;
  color: #fff;
  cursor: pointer;
  position: fixed;
  right: 50px;
  bottom: 50px;
  z-index: 99;
  span {
    line-height: 50px;
    cursor: inherit;
  }
}
</style>