export default function(refValue){
    return {
        mounted(){
            this.$bus.$on("setMainScroll", this.handlerSetMainScroll);
            this.$refs[refValue].addEventListener("scroll", this.handleScroll);
        },
        beforeDestroy() {
            this.$bus.$emit("mainScroll");
            this.$bus.$off("setMainScroll", this.handlerSetMainScroll);
            this.$refs[refValue].removeEventListener("scroll", this.handleScroll);
        },
        methods:{
            handlerSetMainScroll(scrollTop){
                this.$refs[refValue].scrollTop = scrollTop;
            },
            handleScroll() {
                this.$bus.$emit("mainScroll", this.$refs[refValue]);
            },
        }
    }
}