<template>
  <div class="image-loader-container">
    <img
      v-if="!everythingLoaded"
      class="placeholder"
      :src="placeholder"
      alt=""
    />
    <img
      @load="loaded"
      class="origin"
      :src="src"
      alt=""
      :style="{ opacity: imgOpacity, transition: `${duration}ms` }"
    />
  </div>
</template>

<script>
export default {
  props: {
    src: { type: String, required: true },
    placeholder: { type: String, required: true },
    duration: { type: Number, default: 500 },
  },
  data() {
    return {
      imgLoaded: false, //图片是否记载好了
      everythingLoaded: false, //一切是否都加载好了
    };
  },
  computed: {
    imgOpacity() {
      return this.imgLoaded ? 1 : 0; //如果图片加载好了原始图片的opacity的值为1否则为0
    },
  },
  methods: {
    loaded() {
      this.imgLoaded = true;
      setTimeout(() => {
        this.everythingLoaded = true;
        this.$emit("load");
      }, this.duration);
    },
  },
};
</script>

<style lang="less" scoped>
@import "~@/style/mixed.less";
.image-loader-container {
  width: 100%;
  height: 100%;
  overflow: hidden;
  position: relative;
  img {
    .self-fill();
  }
  .placeholder {
    filter: blur(2vh);
  }
}
</style>