<template>
  <div class="blogComment-container" id="blogContainer">
    <MessageArea
      title="评论列表"
      :subTitle="data.total"
      :list="data.rows"
      :isListLoading="isLoading"
      @submit="handleSubmit"
    />
  </div>
</template>

<script>
import { getComment, postComment } from "@/api/blog.js";
import fetchData from "@/mixins/fetchData";
import MessageArea from "@/components/MessageArea";
export default {
  components: { MessageArea },
  mixins: [fetchData({ total: 0, rows: [] })],
  data() {
    return {
      page: 1,
      limit: 10,
    };
  },
  created() {
    this.$bus.$on("mainScroll", this.handlerScroll);
  },
  destroyed() {
    this.$bus.$off("mainScroll", this.handlerScroll);
  },
  computed: {
    //判断是否还有更多数据（这里是有更多数据）
    hasMore() {
      return this.data.rows.length < this.data.total;
    },
  },
  methods: {
    handlerScroll(dom) {
      if (this.isLoading || !dom) {
        //正在加载更多
        return;
      }
      const range = 100;
      const dValue = Math.abs(
        dom.scrollTop + dom.clientHeight - dom.scrollHeight
      );
      if (dValue <= range) {
        this.fetchMore();
      }
    },
    async fetchData() {
      return await getComment(this.$route.params.Id, this.page, this.limit);
    },
    //加载下一页的评论
    async fetchMore() {
      if (!this.hasMore) {
        return;
      }
      this.isLoading = true;
      this.page++;
      const resp = await this.fetchData();
      this.data.total = resp.total;
      this.data.rows = this.data.rows.concat(resp.rows);
      this.isLoading = false;
    },
    async handleSubmit(formData, callback) {
      const resp = await postComment({
        blogId: this.$route.params.Id,
        ...formData,
      });
      this.data.rows.unshift(resp);
      this.data.total++;
      callback("提交成功"); //我这边已经处理完了，子组件可以继续处理接下来的事情了
    },
  },
};
</script>

