<template>
  <div class="pager-container" v-if="maxPage > 1">
    <a @click="handleClick(1)" :class="{ disable: current === 1 }">|&lt;&lt;</a>
    <a @click="handleClick(current - 1)" :class="{ disable: current === 1 }"
      >&lt;&lt;</a
    >
    <a
      @click="handleClick(cur)"
      v-for="(cur, i) in numberArr"
      :key="i"
      :class="{ active: current === cur }"
      >{{ cur }}</a
    >
    <a
      @click="handleClick(current + 1)"
      :class="{ disable: current === maxPage }"
      >&gt;&gt;</a
    >
    <a @click="handleClick(maxPage)" :class="{ disable: current === maxPage }"
      >&gt;&gt;|</a
    >
  </div>
</template>

<style lang="less" scoped>
.pager-container {
  @import "~@/style/common.less";
  display: flex;
  justify-content: center;
  a {
    color: @primary;
    margin: 0 6px;
    cursor: pointer;
    &.disable {
      color: @lightWords;
      cursor: no-drop;
    }
    &.active {
      color: @words;
      cursor: text;
      font-weight: bold;
    }
  }
}
</style>

<script>
export default {
  props: {
    current: { type: Number, default: 1 },
    total: { type: Number, default: 0 },
    limit: { type: Number, default: 10 },
    visibleNumber: { type: Number, default: 10 },
  },
  computed: {
    //总页数（也可以是最大页数）
    maxPage() {
      return Math.ceil(this.total / this.limit);
    },
    //数组显示的最小值
    visibleMin() {
      let min = this.current - Math.floor(this.visibleNumber / 2);
      if (min < 1) {
        min = 1;
      }
      return min;
    },
    //数组显示的最大值
    visibleMax() {
      let max = this.visibleMin + this.visibleNumber - 1;
      if (max > this.maxPage) {
        max = this.maxPage;
      }
      return max;
    },

    //底下的数组
    numberArr() {
      let arr = [];
      for (let i = this.visibleMin; i <= this.visibleMax; i++) {
        arr.push(i);
      }
      return arr;
    },
  },
  methods: {
    //抛出数据
    handleClick(newPage) {
      if (newPage < 1) {
        newPage = 1;
      }
      if (newPage > this.maxPage) {
        newPage = this.maxPage;
      }
      if (newPage === this.current) {
        return;
      }
      // 抛出了一个名字叫pageChange的事件,事件的e(参数,ps:这个名字可以随便取)就是这个newPage
      this.$emit("pageChange", newPage);
    },
  },
};
</script>


