import eventBus from '@/eventBus'
import debounce from '@/utils/debounce'
import defaultGif from '@/assets/common.gif'

//图片数组
let images = [];


// setInterval(() => {
//     console.log(images);
// }, 1000);

//处理图片
function handlerImage(img){
    //判断是否已经处理过了
    if(img.handler){
        return;
    }

    img.dom.src = defaultGif;//先暂时使用默认图片
    //处理图片
    //判断图片在不在视口范围内
    const clientHeight = document.documentElement.clientHeight;//得到页面视口高度
    const positionData = img.dom.getBoundingClientRect();//得到每一个的位置信息
    const imgHeight = positionData.height || 150;//如果图片没有高度的话就给默认值
    if(positionData.top>-imgHeight && positionData.top<=clientHeight){
        //在视口的话
        const tempImg = new Image();
        tempImg.onload = function(){
            img.dom.src = img.src;
        }
        tempImg.src = img.src;
        // img.dom.src = img.src;
        images = images.filter(i=>i !== img)//从数组中移除
        img.handler = true;//设为已经处理
    }
    
}

//希望设置合适的图片
function setImages(){
    for (const img of images) {
        //处理图片
        handlerImage(img);
    }
}
function handlerScroll(dom){
    setImages();
}
eventBus.$on('mainScroll',debounce(handlerScroll,50));

export default {
    inserted(el,binding){
        const img = {
            dom:el,
            src:binding.value,
            handler:false
        }
        images.push(img)
        handlerImage(img)
    },
    unbind(el){
        images = images.filter(img=>img.dom!==el)
    }
} 