import LoadingUrl from "@/assets/loading.svg";
import styles from './loading.module.less';

//判断el里面是否已经有了loadingImg
function getLoadingImage(el){
    return el.querySelector('img[data-identifying=loading]')
}

//创建一个loadingImg元素
function createLoadingImage(){
    const img = document.createElement('img');
    img.dataset.identifying = 'loading';
    img.src = LoadingUrl;
    img.className = styles.loading;
    return img;
}

export default function(el,binding){
    const curImg = getLoadingImage(el); 
    //根据binding.value的值，确定是要删除loadingImg还是要创建一个loadingImg
    if(binding.value){
        if(!curImg){
            const img = createLoadingImage();
            el.appendChild(img);
        }  
    }else{
        curImg&&curImg.remove();
    }
}