<template>
  <div class="aboutContainer" v-loading="loading || !isLoaded">
    <iframe class="aboutContent" :src="src" frameborder="0" @load="isLoaded = true"></iframe>
  </div>
</template>

<script>
import {mapState} from 'vuex';
export default {
  data(){
    return {
      isLoaded:false
    }
  },
  created(){
    this.$store.dispatch('about/fetchAbout');
  },
  computed: mapState('about',{
    src:'data',
    loading:'loading'
  }),

};
</script>

<style scoped lang="less">
.aboutContainer{
  width: 100%;
  height: 100%;
  overflow: hidden;
  position: relative;
  .aboutContent{
    width: 100%;
    height: 100%;
    box-sizing: border-box;
    display: block;
  }
}
</style>