<template>
  <img
    class="avatar-container"
    :src="url"
    :style="{
      width: size + 'px',
      height: size + 'px',
    }"
  />
</template>

<script>
export default {
  props: {
    url: {
      type: String, //表示 url属性必须是一个字符串  如过传入的类型不是字符串，则会报错
      required: true, //表示属性必须传递     如果不填，则会报错
    },
    size: {
      type: Number, //表示类型是数字
      default: 150, //表示默认值，不填写size的话，默认就是150
    },
  },
};
</script>

<style scoped>
/*写一个scoped就会把这些变成带有作用域的样式*/
.avatar-container {
  border-radius: 50%;
  object-fit: cover;
  display: block;
}
</style>
