<template>
  <div class="messageContainer" ref="messageContainer">
    <MessageArea title="留言板" :subTitle="data.total" :isListLoading="isLoading" :list="data.rows" @submit="handleSubmit" />
  </div>
</template>

<script>
import MessageArea from "@/components/MessageArea";
import fetchData from '@/mixins/fetchData';
import * as msgAPI from '@/api/message';
import mainScroll from '@/mixins/mainScroll';
export default {
  data(){
    return{
      page:1,
      limit:10
    }
  },
  components: { MessageArea },
  mixins:[fetchData({total:0,rows:[]}),mainScroll('messageContainer')], 
  computed: {
    //判断是否还有更多数据（这里是有更多数据）
    hasMore() {
      return this.data.rows.length < this.data.total;
    },
  },
  methods:{
    async fetchData(){
      return await msgAPI.getMessage(this.page,this.limit);
    },
    async handleSubmit(data,callBack){
      const resp = await msgAPI.postMessage(data);
      callBack('感谢您的提交')
      this.data.rows.unshift(resp);
    },
    handlerScroll(dom) {
      if (this.isLoading || !dom) {
        //正在加载更多
        return;
      }
      const range = 100;
      const dValue = Math.abs(
        dom.scrollTop + dom.clientHeight - dom.scrollHeight
      );
      if (dValue <= range) {
        this.fetchMore();
      }
    },
     //加载下一页的评论
    async fetchMore() {
      if (!this.hasMore) {
        //没有更多
        return;
      }
      this.isLoading = true;
      this.page++;
      const resp = await this.fetchData();
      this.data.total = resp.total;
      this.data.rows = this.data.rows.concat(resp.rows);
      this.isLoading = false;
    },
  },
  created() {
    this.$bus.$on("mainScroll", this.handlerScroll);
  },
  destroyed() {
    this.$bus.$off("mainScroll", this.handlerScroll);
  },
};
</script>

<style scoped lang='less'>
.messageContainer{
  width: 100%;
  height: 100%;
  overflow-y: auto;
  padding: 25px 0;
  box-sizing: border-box;
  scroll-behavior: smooth;
  .messageArea-container{
    width: 700px;
    margin: 0 auto;
  }
}
</style>