<template>
  <div class="blogCategory-container" v-loading="isLoading">
    <h2>文章列表</h2>
    <ClassList :list="list" @select="handleSelect" />
  </div>
</template>

<script>
import ClassList from "./classList.vue";
import fetchData from "@/mixins/fetchData.js";
import { getBlogType } from "@/api/blog.js";
export default {
  mixins: [fetchData([])],
  components: {
    ClassList,
  },
  computed: {
    categoryId() {
      return +this.$route.params.categoryId || -1;
    },
    limit() {
      return +this.$route.query.limit || 10;
    },
    list() {
      const allArticle = this.data.reduce((a, b) => a + b.articleCount, 0);
      const result = [
        {
          id: -1,
          name: "全部",
          articleCount: allArticle,
        },
        ...this.data,
      ];
      return result.map((it) => ({
        ...it,
        isSelect: this.categoryId === it.id,
        aside: `${it.articleCount}篇`,
      }));
    },
  },
  methods: {
    async fetchData() {
      return await getBlogType();
    },
    handleSelect(rec) {
      if (rec.id === -1) {
        this.$router.push({
          name: "Blog",
        });
      } else {
        this.$router.push({
          name: "CategoryBlog",
          params: {
            categoryId: rec.id,
          },
          query: {
            page: 1,
            limit: this.limit,
          },
        });
      }
    },
  },
};
</script>

<style scoped lang="less">
.blogCategory-container {
  width: 300px;
  box-sizing: border-box;
  padding: 20px;
  position: relative;
  height: 100%;
  overflow-y: auto;
  &::-webkit-scrollbar {
    display: none;
  }
  h2 {
    font-weight: bold;
    font-size: 1em;
    margin: 0;
    letter-spacing: 2px;
  }
}
</style>