<template>
  <Layout>
    <div class="main-container" v-loading="isLoading" ref="mainContainer">
      <BlogDetail :blog="data" v-if="data" />
      <BlogComment v-if="!isLoading" />
    </div>
    <template v-slot:right>
      <div class="toc-container">
        <BlogTOC :blogTOC="data.toc" v-if="data" />
      </div>
    </template>
  </Layout>
</template>

<script>
import { getSoloBlog } from "@/api/blog.js";
import fetchData from "@/mixins/fetchData.js";
import Layout from "@/components/Layout";
import BlogDetail from "./components/BlogDetail.vue";
import BlogTOC from "./components/BlogTOC.vue";
import BlogComment from "./components/BlogComment.vue";
import mainScroll from "@/mixins/mainScroll";
import titleController from '@/utils/titleController';
export default {
  mixins: [fetchData(null), mainScroll("mainContainer")],
  components: { Layout, BlogDetail, BlogTOC, BlogComment },
  methods: {
    async fetchData() {
      const resp = await getSoloBlog(this.$route.params.Id);
      titleController.setRouterTitle(resp.title);
      return resp;
    },
  },
  updated() {
    const hash = location.hash;
    location.hash = "";
    setTimeout(() => {
      location.hash = hash;
    }, 50);
  },
};
</script>

<style scoped lang="less">
.main-container {
  overflow-y: scroll;
  height: 100%;
  box-sizing: border-box;
  padding: 20px;
  position: relative;
  scroll-behavior: smooth;
}
.toc-container {
  overflow-y: scroll;
  height: 100%;
  width: 250px;
  box-sizing: border-box;
  padding: 20px;
  position: relative;
}
</style>